.CategoryTab{
    border:1px solid #a1a1a1;
    /*border-radius: 130px 50px 110px 80px;*/
    width: 48%;
    margin-bottom: 1rem;
    height: 218px;
    overflow: hidden;
    position: relative;
    box-shadow:  1px 1px 3px 2px #333
}

.CategoryTab:nth-of-type(1){ background-color: #e31a4a; border-radius: 130px 50px 110px 80px;}
.CategoryTab:nth-of-type(2){ background-color: #adc91c; border-radius: 80px 150px 70px 160px;}
.CategoryTab:nth-of-type(3){ background-color: #ffec01; border-radius: 95px 130px 100px 170px;}
.CategoryTab:nth-of-type(4){ background-color: #64C5CE; border-radius: 60px 150px 80px 140px;}
.CategoryTab:nth-of-type(5){ background-color: #ED8019; border-radius: 160px 40px 130px 100px;}
.CategoryTab:nth-of-type(6){ background-color: #a1287d; border-radius: 80px 250px 130px 380px;}


.CategoryLinkWrapper{
    width: 100%;
    height: 100%;;
}
.CategoryInnerWrapper{
    width: 100%;
    height: 100%;
    display: flex;
    align-items: flex-end;
}

.TabImg{
    width: 100%;
    height: 100%;;
    position: absolute;
    left:0;
    top: 0;
    
}

.TabImg img{
    object-fit: cover;
    object-position: center;
    width: inherit;
    height: inherit;
}

.CategoryTab h2{
    position: relative;
    z-index: 2;
    color: #fff;
    padding: 10px;
    font-weight: 400;
    font-size: 20px;
    text-align: center;
    letter-spacing: 1px;
    display: flex;
    align-items: center;
    height: 100%;
    justify-content: center;
}


.CategoryTab:nth-of-type(3) h2, .CategoryTab:nth-of-type(4) h2{ 
    color: #000;
}


@media screen and (max-width: 400px) {
    .CategoryTab h2{
        padding: 5px;
        letter-spacing: 0.5px;
    }
}

