body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


html {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
*, *:before, *:after {
  -webkit-box-sizing: inherit;
  -moz-box-sizing: inherit;
  box-sizing: inherit;
}

body{
  /*font-family: 'Roboto', sans-serif;*/
  /*background-color: var(--bgColor);*/
  background-color: #fff;
  font-family: 'Manrope', sans-serif;
}

blockquote,body,div,h1,h2,h3,h4,h5,h6,ol,p,ul{
  margin:0;
  padding:0
}

img{
  width:100%;
  height:auto;
  display:block
}

a{
  text-decoration:none;
  color:inherit
}

ol,ul{
  list-style:none
}

.wrapper{
  max-width:450px;
  padding:0 18px;
  margin:0 auto
}

header{
  width: 100%;
  margin-bottom: 1rem;
  padding-bottom: 5px;
  color: #fff;
}

header .logo{
  width: 220px;
  padding: 14px 0 4px;
  margin: 0 auto;
}

header h1{
  text-align: center;
  font-size: 28px;
  padding: 10px;
  font-weight: 300;
}

aside{
  font-size:10px;
  margin-bottom:1rem;
  color:#000;
  margin-top: 1rem;
  text-align: left;
}

.intro{
  position: fixed;
  width: 100%;
  z-index: 200;
  height: 100vh;
  background-color: #fff;
  transition: left 0.5s;
  left: 0;
  overflow: hidden;
}

.intro-wrapper{
  max-width: 450px;
  padding:2rem;
  margin: 0 auto;
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.intro.hide{
  left: -110vw;
}

.intro .stripes{
  width: 100%;
  position: absolute;
  top: 40%;
  transform: translateY(-50%);
}


.intro .stripes .stripe{
  width : 100%;
  height: 20px;
  margin-bottom: 10px;
  transition: left 0.5s;
  position: relative;
  left: -100%;
}

.intro .stripes .stripe.show{
  left: 100%;
}

.intro .stripes .stripe.red{ background-color: #e31a4a;}
.intro .stripes .stripe.green{ background-color: #adc91c;}
.intro .stripes .stripe.blue{ background-color: #64C5CE;}
.intro .stripes .stripe.orange{ background-color:#ED8019 ;}
.intro .stripes .stripe.purple{ background-color:#a1287d ;}



.enter{
  align-self: center;
  padding: 10px 5px;
  text-align: center;
  opacity: 1;
  transition: opacity 0.5s;
  margin-top: auto;
  margin-bottom: 8rem;
  width: 100%;;
}

.enter button{
  width: 80%;
  border: 1px solid #000;
    padding: 0.5rem 1rem;
    background: #e31a4a;
    color: #fff;
    font-size: 14px;
    border-radius: 20px;
    box-shadow:  1px 1px 3px 2px #a1a1a1;
  
}

.enter.hide{
  opacity: 0;
}

.intro-logo{
  opacity: 1;
  transition: opacity 0.8s;
  margin-top: auto;
}


.intro-logo.hide{
  opacity: 0;
}

.filters-wrapper{
  width: calc(100% + 18px);
}

.filters{
  width: 100%;
}

.filters ul{
  display:-webkit-flex;
  display:flex;
  margin-bottom:.5rem;
  max-width:100%;
  overflow-x:scroll;
  -ms-scroll-snap-type:x mandatory;
  scroll-snap-type:x mandatory;
  -ms-overflow-style:none;
  scrollbar-width:none;
  width: fit-content;
}

.filters ul::-webkit-scrollbar{
  display:none
}

.filters ul li{
  border:1px solid #705D56;
  padding:6px 8px;
  font-size:12px;
  background-color:#6E4555;
  color:#fff;
  scroll-snap-align:start;
  border-radius: 10px 0;
  flex-shrink: 0;
  letter-spacing: 0.5px;
}

.filters ul li.active{
  background-color:#048BA8;
  border:1px solid #fff;
}

.filters ul li:not(:last-of-type){
  margin-right:.5rem
}


@media screen and (max-width:400px){
  .wrapper{
    padding: 0 10px;
  }
  .filters-wrapper{
    width: calc(100% + 10px);
  }
}