.ProductListItem{
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: nowrap;
    margin-bottom: 6px;
    color: #000;
    position: relative;
}
.ProductListItem:after{
    content: '';
    width: 90%;
    border-top: 1px solid #fff;
    position: absolute;
    bottom: 0;
    left: 5%;
}

.ProductListItemImage{
    width: calc(100% + 16px);
    height: 250px;
    /*margin-bottom: 1rem;*/
    left: -8px;
    position: relative;
}

.ProductListItemImage img{
    object-fit: cover;
    object-position: center;
    width: inherit;
    height: 100%;
}

.ProductListItemName{
    max-width: calc(100% - 70px);
    font-size: 18px;
    line-height: 22px;
    text-align: left;
    position: relative;
    font-weight: 300;
    padding: 4px 4px 4px 1.5ch;
}

.ProductListItemName img{
    position: absolute;
    width: 60px;
    top: 0;
    left: 95%;
    z-index: 5;
}

.ProductListItemName b{
    display: block;
}

.ProductListItemPrice{
    width: 60px;
    align-self: stretch;
    background-color: #548D2A;
    color: #fff;
    padding: 5px;
    /*border: 1px solid;*/
    position: relative;
    z-index: 20;
    font-size: 19px;
    font-weight: 300;
    display: flex;
    align-items: center;
    border-radius: 20px;
    text-align: center;
    margin-right: 4px;
}


.ProductListItemPrice > div:first-child{ 
    width: 100%;
    text-align: center;
}

.ProductListItemDescription{
    width: 100%;
    font-family: sans-serif;
    font-size: 12px;
    line-height: 15px;
    padding-left:0px;
    position: relative;
    margin-top: 2px;
    padding-top: 2px;
    border-top: 1px solid #048BA8;;
}

.ProductListItemDescription:before{
    content: '';
    position: absolute;
    width: 100%;
    border-top: 1px solid var(--priceColor);
    left: 0;
    top: 0;
}

@media screen and (max-width:362px){
    .ProductListItemName{
        font-size: 16px;
        line-height: 20px;
    }
}
