.FooterWrapper{
    max-width: 450px;
    margin: 0 auto;
    padding: 2rem 12px 5px;
}

.SocialWrap{
    width: 100%;  
}

.SocialItem{
    display: flex;
    width :100%;
    align-items: center;
}

.SocialItem > div:first-of-type{
    width: 50px;
    flex-shrink: 0;
    flex-grow: 0;
    font-size: 2rem;
    margin-right: 5px;
}

.SocialItem > h4{
    flex-grow: 1;
    font-size: 20px;
    line-height: 22px;
    font-weight: 100;
}


.Address{
    display: block;
    text-align: center;
    align-items: center;
    position: relative;
    width: 100%;
}

.Address address{
    font-style: normal;
    font-size: 20px;
    line-height: 22px;
    font-weight: 300;
}

.Footer address span{
    display: block;
    font-size: 16px;
}

.Social{
    display: flex;
    justify-content: space-around;
    width: 60%;
    margin: 0 auto;
    align-items: center;
    margin-top: 1rem;
}
.Social  i{
    font-size: 2rem;
}

.PhoneWrapper{
    background-color: #e31a4a;
    padding: 1rem;
}

.Phone{
    max-width: 450px;
    margin: 0 auto;
    padding: 10px 0;;
    width: 100%;
    color: #fff;
}


.Phone  a{
    display: block;
    font-weight: 500;
    letter-spacing: 0.65px;
    margin-top: 6px;
}

.FooterLogo{
    max-width: 450px;
    width: 90%;
    margin: 0 auto;
    padding: 10px;
}

.Developer{
    font-size: 12px;
    padding: 0.5rem 0;
    position: relative;
    font-weight: 100;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    background-color: #416E21;
}

.Developer h6{
    margin-left: 1ch;
}


.Developer h6{
    font-size: 16px;
    font-weight: 100;
}


@media screen and (max-width:390px){
    .SocialWrap{
        padding: 0 6px;
    }
    .Social  i{
        font-size: 25px;
    }
    .SocialItem > div:first-of-type{
        font-size: 25px;
    }
    .SocialItem > h4, .Address address{
        font-size: 18px;
        line-height: 21px;
    }
    .Address address span{
        font-size: 14px;
    }
    
}

@media screen and (max-width:355px){
    .Developer{
        font-size: 12px;
    }
    .Developer h6{
        font-size: 14px;
    }
}